<template>
    <div class="view-style">
        <div class="back-style" :style="`background-image:url(${require('@/assets/image/main_back/home_image_2_a.png')})`"></div>
        <div class="home-style" @click="routerEvent('MainGame')"><i class="icon-back1" style="vertical-align:bottom"/></div>
        <div class="main">
            <div class="lvstyle">
                <p v-html="lv"></p>
            </div>
            <div class="attributestyle">
                <div class="attributestyle_title"><i class="icon-tl" style="vertical-align:bottom"/>基础属性<i class="icon-tr" style="vertical-align:bottom"/></div>
                <!-- <div class="attributestyle_title" :style="`background-image:url(${require('@/assets/jichushuxing.webp')})`"></div> -->
                <div>
                    <div class="attributestyle_a"><strong>神眷阶位：</strong>{{userinfo.lv}}{{userinfo.lvdegree.length==3?"(可献祭以升阶)":''}}</div>
                    <div class="attributestyle_a"><strong>护命次数：</strong>{{userinfo.lv=="告密者"?"壹":userinfo.lv=="海玲珑"?"叁":"玖"}}</div>
                    <div class="attributestyle_a"><strong>幸运值：</strong>{{userinfo.lv=="告密者"?"伍":userinfo.lv=="海玲珑"?"拾":"壹拾贰"}}</div>
                </div>
                <div class="attributestyle_title"><i class="icon-tl" style="vertical-align:bottom"/>当前状态<i class="icon-tr" style="vertical-align:bottom"/></div>
                <div>
                    <div v-if="userstatus.length<1" class="attributestyle_b">无</div>
                </div>
                <div class="attributestyle_title"><i class="icon-tl" style="vertical-align:bottom"/>神明祝福<i class="icon-tr" style="vertical-align:bottom"/></div>
                <div>
                    <div v-if="godsblessing.length<1" class="attributestyle_c">无</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

var _  = require('lodash')
export default {
    name: "UserAttribute",
    data(){
        return{
            lv:'',
            statue:'',
            userinfo:{
                lv:'',
                lvdegree:''
            },
            userstatus:[],
            godsblessing:[],
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        routerEvent(e){
            this.$router.push({name:e})
        },
        init(){
            this.userinfo = this.$store.state.archive.archive
            var a = _.findIndex(this.userinfo.lvdegree,o=>{return o == 1})
            var b = _.findIndex(this.userinfo.lvdegree,o=>{return o == 2})
            var c = _.findIndex(this.userinfo.lvdegree,o=>{return o == 3})
            this.lv = `<span style='${a!==-1?'color:#00ffd0':''}'>${this.userinfo.lv[0]}</span><span style='${b!==-1?'color:#00ffd0':''}'>${this.userinfo.lv[1]}</span><span style='${c!==-1?'color:#00ffd0':''}'>${this.userinfo.lv[2]}</span>`
        }
    }
};
</script>

<style lang="scss" scoped>
.view-style{
    width: 100%;
    height: 100%;
}
.back-style {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    // filter: blur(0.3vh);
    // filter: grayscale(80%);
}
.home-style{
    position: absolute;
    z-index: 9;
    left: 2%;
    top: 10px;
    color: rgba(127, 255, 212, 0.527);
    font-size: 20px;
    font-family: "Goudy Old Style", Courier, monospace;
    transition: 0.2s all;
    cursor: pointer;
}
.home-style:hover{
    letter-spacing: 3px;
    color: rgba(127, 255, 212, 1);
}
.main{
    position: absolute;
    z-index: 2;
    width: 90%;
    height: 80%;
    // border: 1px solid black;
    top: 10%;
    left: 5%;
}
.lvstyle{
    position: absolute;
    z-index: 2;
    width: 30%;
    height: 100%;
    font-size: 120px;
    font-family: Ssxy;
    letter-spacing: 60px;
    word-break: break-all;
    writing-mode: tb-rl;
    // border: 1px solid black;
    line-height: 500px;
}
.attributestyle{
    position: absolute;
    z-index: 2;
    left: 30%;
    width: 30%;
    height: 100%;
    // border: 1px solid black;
    font-family: "幼圆", Courier, monospace;
}
.attributestyle_title{
    font-size: 40px;
    letter-spacing: 10px;
    margin-top: 20px;
}
.attributestyle_a{
    font-size: 25px;
    margin-top: 20px;
    text-align: left;
}
.attributestyle_b{
    font-size: 25px;
    margin-top: 20px;
}
.attributestyle_c{
    font-size: 25px;
    margin-top: 20px;
}
.embedstyle{
    border: 1px solid black;
}
</style>